import React, {Component, Suspense, lazy} from 'react';
import './App.css';
import Nav from './components/Nav/Nav';
import {NavLink, Route, Routes} from "react-router-dom";
import UsersContainer from "./components/Users/UsersContainer";
import ContentContainer from "./components/Content/ContentContainer";
import HeaderContainer from "./components/Header/Header-Container";
import LoginPage from "./components/Login/Login";
import {connect} from "react-redux";
import 'antd/dist/antd.css'
import {initializeApp} from "./redux/App-reducer";
import Preloader from "./components/common/Preloader/Preloader"; /*когда импортируем по дефолту можно импортировать под другим именем*/

import {Breadcrumb, Layout, Menu} from 'antd'
import {LaptopOutlined, NotificationOutlined, UserOutlined} from '@ant-design/icons'
import classes from "./components/Nav/Nav.module.css";
import StartGame from "./components/Game/startGame";
import Game from "./components/Game/Game";

const DialogsContainer = React.lazy(() =>
    import('./components/Dialogs/DialogsContainer'));

const {SubMenu} = Menu
const {Content, Footer, Sider} = Layout

class App extends Component {
    componentDidMount() {
        this.props.initializeApp();
    }

    /*если мы не проинициализировались тогда верни крутилку прелоадер*/
    render() {
        if(!this.props.initialized) {
            return <Preloader/>
        }

        return (
            <Layout>
                <HeaderContainer/>
                <Content style={{padding: '0 50px'}}>
                    {/*<Breadcrumb style={{margin: '16px 0'}}>
                        <Breadcrumb.Item>Home</Breadcrumb.Item>
                        <Breadcrumb.Item>List</Breadcrumb.Item>
                        <Breadcrumb.Item>App</Breadcrumb.Item>
                    </Breadcrumb>*/}
                    <Layout className="site-layout-background" style={{padding: '24px 0'}}>
                        <Sider className="site-layout-background" width={200}>
                            <Menu
                                mode="inline"
                                /*  defaultSelectedKeys={['7']}*/
                                  defaultOpenKeys={['sub1']}
                                style={{height: '100%'}}
                            >
                                <SubMenu key="sub1" icon={<UserOutlined/>} title="My Profile">
                                    <Menu.Item key="1"> <NavLink to="/profile">Profile</NavLink></Menu.Item>
                                    <Menu.Item key="3"><NavLink to="/users">Users</NavLink></Menu.Item>
                                    {/*<Menu.Item key="2"> <NavLink to="/dialogs">Messages</NavLink></Menu.Item>*/}
                                </SubMenu>
                                <SubMenu key="sub2" icon={<LaptopOutlined/>} title="Game">
                                    <Menu.Item key="4"><NavLink to="/game/1" >Game</NavLink></Menu.Item>
                                </SubMenu>
                                {/*<SubMenu key="sub3" icon={<NotificationOutlined/>} title="Game">

                                </SubMenu>*/}
                            </Menu>
                        </Sider>
                        <Content style={{padding: '0 24px', minHeight: 280}}>

                            <Routes >
                                <Route path="/profile/*"
                                       element={<ContentContainer/>}/>
                                <Route path="/SocialNetwork"
                                       element={<StartGame/>}/>
                                <Route path="/login"
                                       element={<LoginPage/>}/>

                                <Route path="/profile/:userId"
                                       element={<ContentContainer/>}/>

                                <Route path="/startGame"
                                       element={<StartGame/>}/>

                                <Route path="/game/*"
                                       element={<Game/>}/>

                                <Route path="/dialogs/*"
                                       element={
                                           <Suspense fallback={<Preloader/>}>
                                               <DialogsContainer/>
                                           </Suspense>
                                       }/>

                                <Route path="/users"
                                       element={<UsersContainer/>}/>
                            </Routes>

                        </Content>
                    </Layout>
                </Content>

            </Layout>
            )

    }
}

const mapStateToProps = (state) => ({
    initialized: state.app.initialized

})

export default connect(mapStateToProps, {initializeApp}) (App);